.container-pets{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(60, 78, 179);
    padding: 30px 0;
    color: white;
    width: 100%;
}
.container-pets .section-pets{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0;
    width: 90%;
}
.sectionPets-contain-title{
    width: 100%;
    margin-bottom: 30px;
}
.sectionPets-contain-title h2{
    text-align: center;
    margin-bottom: 0;
}

.container-pets .section-pets .contain-main-scroll{
    display: flex;
    overflow: auto;
    scroll-snap-type: x mandatory;
    gap: 15px;
}
