footer{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    align-items: center;
    background-color: #0e2a47;
    color: white;
}
footer .logo-footer{
    width: 100%;
}
footer .logo-footer p{
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1px;
}
footer .rrss-footer{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    font-size: 16px;
    margin-top: -10px;
}
footer .rrss-footer ul{
    display: flex;
    justify-content: center;
    gap: 20px;
    padding: 0;
}
footer .links-footer{
    display: flex;
    justify-content: center;
    width: 100%;
}
footer .links-footer ul{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    gap: 10px;
}
footer .links-footer ul li a svg{
    margin-right: 6px;
}