@keyframes background {
    100% {background-size: 4000px 1000px;}
}

.main-form{
    display: grid;
    height: auto;
    margin: -10px 0;
    padding: 30px 24px;
    background-image: url("../assets/img/Wave.svg");
    background-size: 2000px 1000px;
    background-position: -500px 0;
    color: #f9f9f9;
    font-family: "Roboto", sans-serif;
    animation: background 20s infinite alternate linear;
    justify-items: center;
}
.containerPage-form{
    width: 100%;
    height: auto;
    padding: 30px 10px;
    border-radius: 22px;
    background: #16161661;
    text-align: center;
}

.containerPage-form > h2{
    font-size: 25px;
    font-weight: 500;
    margin: 0 0 4px;
}

.form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    gap: 12px;
    padding: 20px;
    margin: auto;
}
.form input{
    padding: 15px;
    width: 100%;
    align-items: center;
}
.form input,
.form button{
    width: 100%;
    height: 40px;
    outline: none;
    padding: 10px;
    font-family: inherit;
    font-size: 14px;
    border-radius: 8px;
}
.form input{
    background: rgba(255, 255, 255, 0.902);
    border: 2px solid rgba(255, 255, 255, 0.1);
    font-size: 14px;
    color: #302f2f;
}
.select{
    width: 100%;
    height: 40px;
    border: none;
    background-color: rgba(255, 255, 255, 0.902);
}
.form fieldset{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.form .checkbox{
    height: 20px;
}
.form textarea{
    width: 100%;
    background-color: rgba(255, 255, 255, 0.902);
}
.form .file-area{
    border: 2px dashed #fff;
    width: 100%;
    height: 200px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.form .file-icon{
    height: 40%;
    color: white;
}
.form .file-icon svg{
    height: 60px;
}
.form #file{
    display: none;
}
.form .inputFile{
    background: transparent;
    border: none;
    font-size: 14px;
    color: white;
    width: 100%;
}
.form .file-area:hover{
    border: 2px solid #fff;
}
.form .file-area .btn-file{
    width: 50%;
}
.form input:focus{
    border-color: #157ae1;
}
.form input:focus ~ span{
    color: #157ae1;
}
.form button{
    cursor: pointer;
    background: #157ae1;
    color: #f9f9f9;
    border: 0;
    font-weight: 600;
    letter-spacing: 2px;
}
.form a{
    color: #157ae1;
    font-size: 12px;
    text-align: left;
}

/* Section pie de imagen */
.section-pie{
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 5px 10px;
    font-size: 14px;
    border-radius: 5px;
    background-color: #e9f0ff82;
}
.section-pie span{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.section-pie span svg{
    height: 20px;
}
.pieInfo-image{
    display: flex;
    gap: 10px;
    align-items: center;
}