.container-home{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.banner{
    display: absolute;
    width: 100%;
    height: 160px;
    position: relative;
    background-image: url('../assets/img/banner-home.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 30px;
    margin-bottom: 0;
}
.banner h3{
    text-align: center;
}

.banner-tips{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 0 20px;
    height: 160px;
    margin-bottom: 30px;
    background-color: cornflowerblue;
    margin-bottom: 0;
}
.banner-tips h3{
    text-align: center;
    color: white;
    font-size: 20px;
}

.section-help{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(60, 78, 179);
    padding: 30px 0;
    color: white;
    width: 100%;
}
.sectionHelp-contain-title{
    width: 60%;
    margin-bottom: 30px;
}
.sectionHelp-contain-title h2{
    text-align: center;
}

.contain-main{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 8px;
}


/* Por sobre las pantallas de 450px */
@media (width >= 450px ){
    .contain-main{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        width: 90%;
        gap: 15px;
    }
}