/* NavBar - Logo */
.navBar{
    background: #0e2a47;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.nav_logo{
    font-weight: bold;
    font-size: 20px;
    margin: 15px;
    color: white;
}
/* NavBar - Links */
.nav_items a{
    margin: 15px;
    color: white;
    position: relative;
    text-decoration: none;
}
.nav_toggle{
    display: none;
}

/* Menu Responsive con toggle */
@media (max-width: 768px) {
    .nav_items{
        position: absolute;
        top: 50px;
        left: 0;
        background: #0e2a47;
        display: flex;
        flex-direction: column;
        width: 100%;
        height: -webkit-fill-available;
        transform: translateX(-100%);
        transition: 0.3s ease all;
    }
    .nav_items.open{
        transform: translateX(0) !important;
        z-index: 1;
    }
    .nav_toggle{
        display: flex !important;
        flex-direction: column;
        margin: 15px;
    }
    .nav_toggle span{
        width: 25px;
        height: 2px;
        background: white;
        margin-bottom: 5px;
        border-radius: 2px;
        transform-origin: 5px 0px;
        transition: all 0.2s linear;
    }
    /* Animacion de icon hamburguer a X */
    .nav_toggle.open > span{
        transform: rotate(45deg) translate(0px, 0px);
    }
    .nav_toggle.open > span:nth-child(2){
        display: none;
    }
    .nav_toggle.open > span:nth-child(3){
        transform: rotate(-45deg) translate(-5px, 1px);
    }
}