.contain-main .card-admin{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 90%;
}
.contain-cardImg-admin{
    width: 40px;
    height: 40px;
}
.contain-cardImg-admin img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.contain-cardDetail-admin{

}
.contain-cardButtons-admin{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.contain-cardButtons-admin svg{
    font-size: 18px;
}
.contain-cardButtons-admin .AdminEdit{
    background-color: hsl(45deg 100% 51%);
    color: hsl(37deg 58% 27%);
}
.contain-cardButtons-admin .AdminDelete{
    background-color: hsl(354deg 70% 54%);
    color: white;
}