.contain-main .card{
    position: relative;
    display: inline-block;
    text-align: center;
    max-width: 50%;
}
.contain-cardImg{
    width: 150px;
    height: 200px;
}
.contain-cardImg img{
    width: 100%;
    height: 100%;
}
.title-card {
    position: absolute;
    top: 85%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(58, 57, 57, 0.505);
    width: 100%;
    font-weight: 600;
    letter-spacing: 1px;
}