.main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px 0;
}
.container-img{
    width: 300px;
}
.container-img img{
    width: 100%;
}

.main .container-info{
    padding: 0 20px;
}
.main .container-info h1{
    text-align: center;
}
.main .container-info .desc-pet{
    font-size: 17px;
    line-height: 25px;
    margin-bottom: 30px;
}
.main .container-info p{
    margin: 8px;
}
.main .container-info p > span{
    font-weight: bold;
}

.main .container-adopt{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: cornflowerblue;
    color: white;
    padding: 20px 40px;
    margin-top: 10px;
    width: 100%;
}
.main .container-adopt h2{
    font-size: 24px;
    text-align: center;
}
.main .container-adopt button{
    display: inline-flex;
    width: 180px;
    color: white;
    background-image: linear-gradient(170deg, #ff58fe 0, #ff50fc 25%, #f246f9 50%, #e23df6 75%, #d235f4 100%);    
    border: none;
    border-radius: 30px;
    align-items: center;
    justify-content: center;
    letter-spacing: 3px;
    font-size: 16px;
    font-weight: bold;
    padding: 14px;
    margin: 10px 0;
    cursor: pointer;
}
.main .container-adopt button:hover{
    display: inline-flex;
    width: 180px;
    color: rgb(48, 46, 46);
    background: white;    
    border: 2px solid #ff58fe;
    border-radius: 30px;
    text-transform: capitalize;
    align-items: center;
    justify-content: center;
    letter-spacing: 3px;
    font-weight: bold;
    padding: 14px;
    margin: 10px 0;
    transition: ease all .2s;
}
.main .container-adopt svg{
    margin-left: 5px;
    height: 18px;
}
.main .container-adopt button:hover svg{
    margin-left: 5px;
    width: 24px;
    height: 24px;
    background-color: #ff58fe;
    border-radius: 50%;
    padding: 2px;
}

.main .banner-help{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: cornflowerblue;
    color: white;
    padding: 20px 40px;
    margin-top: 10px;
    width: 100%;
}
.main .banner-help h2{
    font-size: 24px;
    text-align: center;
}
.main .banner-help button{
    display: inline-flex;
    width: 180px;
    color: white;
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    border: none;
    border-radius: 30px;
    align-items: center;
    justify-content: center;
    letter-spacing: 3px;
    font-size: 15px;
    font-weight: bold;
    padding: 14px;
    margin: 10px 0;
    cursor: pointer;
}
.main .banner-help button:hover{
    display: inline-flex;
    width: 180px;
    color: rgb(48, 46, 46);
    background: white;    
    border: 2px solid #ff58fe;
    border-radius: 30px;
    text-transform: capitalize;
    align-items: center;
    justify-content: center;
    letter-spacing: 3px;
    margin: 10px 0;
    transition: ease all .2s;
}
.main .banner-help svg{
    margin-left: 10px;
    height: 18px;
}
.main .banner-help button:hover svg{
    margin-left: 5px;
    width: 24px;
    height: 24px;
    background-color: #ff58fe;
    border-radius: 50%;
    padding: 2px;
}