*{
    box-sizing: border-box;
}

html, body{height: 100%;}

@keyframes background {
    100% {background-size: 4000px 1000px;}
}

/* Container de login */
.container-login{
    display: grid;
    place-items: center;
    height: 92vh;
    margin: -10px 0;
    padding: 0 24px;
    background-image: url("../assets/img/Wave.svg");
    background-size: 2000px 1000px;
    background-position: -500px 0;
    color: #f9f9f9;
    font-family: "Roboto", sans-serif;
    animation: background 20s infinite alternate linear;
}

.login{
    width: 90%;
    height: 90%;
    padding: 30px 22px 60px;
    border-radius: 22px;
    background: #161616;
    text-align: center;
}

/* Imagen de perfil */
.avatar{
    margin: 0 auto 16px;
    border-radius: 50%;
    background: linear-gradient(-45deg, #157ae1, #c7a1ff);
    padding: 2px;
    width: 90px;
    height: 90px;
}
.avatar > img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    border: 4px solid #161616;
}

.login > h2{
    font-size: 25px;
    font-weight: 500;
    margin: 0 0 4px;
}
.login > h3{
    color: rgba(255, 255, 255, 0.38);
    margin: 0 0 30px;
    font-weight: 500;
    font-size: 15px;
    margin-top: 10px;
    margin-bottom: 12px;
}
/* Login formulario */
.login-form{
    display: grid;
    gap: 12px;
    place-items: center;
    width: 100%;
    margin: 0;
}
.textbox{
    width: 100%;
    position: relative;
}
.textbox span{
    position: absolute;
    top: 50%;
    left: 16px;
    translate: 0 -50%;
    color: rgba(255, 255, 255, 0.38);
}
.textbox span svg{
    width: 28px;
    text-align: center;
    color: rgba(255, 255, 255, 0.38);
}
.login-form input,
.login-form button{
    width: 100%;
    height: 40px;
    outline: none;
    padding: 0;
    font-family: inherit;
    font-size: 14px;
    border-radius: 8px;
}
.login-form input{
    background: transparent;
    border: 2px solid rgba(255, 255, 255, 0.1);
    font-size: 14px;
    padding: 0 20px 0 50px;
    color: inherit;
}
.login-form input:focus{
    border-color: #157ae1;
}
.login-form input:focus ~ span{
    color: #157ae1;
}
.login-form button{
    cursor: pointer;
    background: #157ae1;
    color: #f9f9f9;
    border: 0;
    font-weight: 600;
    letter-spacing: 2px;
}
.login-form a{
    color: #157ae1;
    font-size: 12px;
    text-align: left;
}

/* Seccion extra */
.register {
    font-size: 14px;
}
.register a{
    font-size: 15px;
    font-weight: bold;
}

/* GoogleBtn */
.or{
    font-size: 12px;
    font-weight: normal;
    margin: 0;
}
.container-btns{
    width: 100%;
}
.container-btns .google-btn{
    width: 100%;
    height: 40px;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: white;
    box-shadow: rgb(0 0 0 / 20%) 0px 3px 1px -2px, rgb(0 0 0 / 14%) 0px 2px 2px 0px, rgb(0 0 0 / 12%) 0px 1px 5px 0px;
    margin: 0 0 20px 0;
    color: #3c4043;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.container-btns .google-btn > img{
    width: 20px;
    height: 20px;
    object-fit: cover;
}
.container-btns .google-btn > span{
    margin-left: 7px;
    font-size: 12px;
    letter-spacing: 0.10px;
}


/* Por sobre las pantallas de 450px */
@media (width >= 450px ){
    .login{width: 350px;
    }
    .avatar{
        width: 120px;
        height: 120px;
    }
    .login > h2{
        font-size: 35px;
    }
    .login > h3{
        font-size: 20px;
    }
    .login-form{
        gap: 18px;
    }
    .login-form input,
    .login-form button{
        height: 50px;
    }
}